import { Element } from '../snap';

const alignGroup = <T extends Element>({ g, x, y, s }: { g: T; x?: number; y?: number; s?: number }): T => {
    var currentTransform = g.transform().local;
    var gTBox = g.getTBox();

    var moveX = x !== undefined ? fix((x - gTBox.cx) / gTBox.a) : 0;
    var moveY = y !== undefined ? fix((y - gTBox.cy) / gTBox.d) : 0;
    if (s) {
        var scaleX = s / gTBox.a;
        var scaleY = s / gTBox.d;
        g.transform(currentTransform + 't' + [moveX, moveY] + ' s' + [scaleX, scaleY]);
    } else {
        g.transform(currentTransform + 't' + [moveX, moveY]);
    }
    return g;
    //2018.04.25 특정 PC에서 alignGroup 오류 해결(moveX, moveY값이 3e-9 형태로 계산되는 것)
    function fix(d: number) {
        return Math.round(d * 1e5) / 1e5;
    }
};

export default alignGroup;
